document.addEventListener( 'DOMContentLoaded', function() {

	const WFSVideoDefault = new WFSvideo({
		conteiner: '#wfsv-default',
		elements: '.wfsv-link',
		modalBox: '#wfsv-video',
		videoBox: '.wfsv-holder-video',
		openClass: 'open-video',
		title: true,
		type: true,
		titleClass: '.wfsv-holder-description .wfsv-title',
		navigation: {
			start: false,
			nextBtn: ".wfsv-next",
			prevBtn: ".wfsv-prev",
			closeBtn: ".wfsv-close",
			disableBtn: 'hedden-btn'
		},
	})

	const WFSVideoSwitcher = new WFSvideo({
		conteiner: '#wfsv-switcher',
		elements: '.wfsv-link',
		modalBox: '#wfsv-video',
		videoBox: '.wfsv-holder-video',
		openClass: 'open-video',
		title: true,
		type: true,
		titleClass: '.wfsv-holder-description .wfsv-title',
		navigation: {
			nextBtn: ".wfsv-next",
			prevBtn: ".wfsv-prev",
			closeBtn: ".wfsv-close",
			disableBtn: 'hedden-btn'
		},
		pagination: {
			start: true,
			el: '.wfsv-pagination',
			type: 'switcher',
			htmlEl: '',
			clickable: true
		},
	})

	const WFSVideoProgress = new WFSvideo({
		conteiner: '#wfsv-progress',
		elements: '.wfsv-link',
		modalBox: '#wfsv-video',
		videoBox: '.wfsv-holder-video',
		openClass: 'open-video',
		title: true,
		type: true,
		titleClass: '.wfsv-holder-description .wfsv-title',
		navigation: {
			nextBtn: ".wfsv-next",
			prevBtn: ".wfsv-prev",
			closeBtn: ".wfsv-close",
			disableBtn: 'hedden-btn'
		},
		pagination: {
			start: true,
			el: '.wfsv-pagination',
			type: 'progress',
			htmlEl: '',
			clickable: true
		},
	})

	const WFSVideoFraction = new WFSvideo({
		conteiner: '#wfsv-fraction',
		elements: '.wfsv-link',
		modalBox: '#wfsv-video',
		videoBox: '.wfsv-holder-video',
		openClass: 'open-video',
		title: true,
		type: true,
		titleClass: '.wfsv-holder-description .wfsv-title',
		navigation: {
			nextBtn: ".wfsv-next",
			prevBtn: ".wfsv-prev",
			closeBtn: ".wfsv-close",
			disableBtn: 'hedden-btn'
		},
		pagination: {
			start: true,
			el: '.wfsv-pagination',
			type: 'fraction',
			htmlEl: '',
			clickable: true
		},
	})

	const WFSVideoDestroy = new WFSvideo({
		conteiner: '#wfsv-destroy',
		elements: '.wfsv-link',
		modalBox: '#wfsv-video',
		videoBox: '.wfsv-holder-video',
		openClass: 'open-video',
		title: true,
		type: true,
		titleClass: '.wfsv-holder-description .wfsv-title',
		navigation: {
			nextBtn: ".wfsv-next",
			prevBtn: ".wfsv-prev",
			closeBtn: ".wfsv-close",
			disableBtn: 'hedden-btn'
		},
	})

	const DESTROYBUTTON =  document.querySelector('.wfsv-destroy-link');

	if(DESTROYBUTTON){
		DESTROYBUTTON.addEventListener('click', (e) => {
			e.preventDefault();
			WFSVideoDestroy.destroy();
		});
	}

});
